    methods: {
      submit(){
        if(localStorage.getItem("username")!=null){
          this.dialog_err=true;
          // setTimeout(() => {
          //   this.$router.push("..//location");
          // }, 3000);
        }else if(this.firstName=="" || this.lastName=="" || this.userName=="" || this.email=="" || this.password == ""){
          alert("ente all your information")
        }else if(this.firstName || this.lastName || this.userName || this.email || this.password != ""){
          if(this.password != this.passwordgood){
            alert("your paswords is not Identical")
          }else{
            localStorage.setItem("firstname",this.firstName);
            localStorage.setItem("lastname",this.lastName);
            localStorage.setItem("username",this.userName);
            localStorage.setItem("email",this.email);
            localStorage.setItem("password",this.password);
            this.$router.push("..//location");
          }
        }
      },
      closeDialog(){
        this.dialog=false;
        this.dialog_err=false;
      },
      closeDialogErr(){
        this.dialog=false;
        this.dialog_err=false;
        this.loginopen=false;
        this.logupopen=true;
        this.firstName = null;
        this.lastName = null;
        this.userName = null;
        this.email = null;
        this.password = null;
        this.passwordgood = null;
        this.check = null;
        this.checkPassword = null;

      },
      login(){
        this.loginopen=true;
        this.logupopen=false;
      },
      logup(){
        this.loginopen=false;
        this.logupopen=true;
        this.check = null;
        this.checkPassword = null;
      },
      go(){
        if((this.check == localStorage.getItem("username") || this.check == localStorage.getItem("email")) && this.checkPassword == localStorage.getItem("password")){
          this.$router.push("..//location");
        }else if(this.check == "" || this.checkPassword == ""){
          alert("enter your usernam and your password")
        }else if((this.check != localStorage.getItem("username") || this.check != localStorage.getItem("email")) && this.checkPassword != localStorage.getItem("password")){
