<template>
    <v-row>
        <v-dialog
      v-model="dialog_err"
      width="500"
    >
    <v-card>
        <v-card-text style="text-align: center;font-size:18px;">
            <span>Choose file: </span><input id="newimage" type="file">
        </v-card-text>
        <v-action style="text-align:ltr;">
          <v-btn @click="closeDialog" style="margin:0px 0px 5px 10px;background-color:grey;color:white;">cancel</v-btn>

<v-btn id="logined" @click="changePorfile" style="float:right;margin-right:10px;background-color:red;color:white;">change</v-btn>
        </v-action>
      </v-card>
    </v-dialog>
    
    <!-- change name -->

    <v-dialog
      v-model="editname"
      width="500"
    >
    <v-card>
        <v-card-text style="text-align: center;font-size:18px; padding-top:10px;">
        <v-text-field
            label="First name"
            solo
            required
            :maxlength="9"
            v-model="firstName"
          ></v-text-field>
          <v-text-field
            label="Last name"
            solo
            required
            :maxlength="9"
            v-model="lastName"
          ></v-text-field>
        </v-card-text>
        <v-action style="text-align:ltr;">
          <v-btn @click="editname = false; lastName = '' ; firstName = '';" style="margin:0px 0px 5px 10px;background-color:grey;color:white;">cancel</v-btn>
          <v-btn @click="changeName" style="float:right;margin-right:10px;background-color:red;color:white;">change</v-btn>
        </v-action>
      </v-card>
    </v-dialog>

<!-- change password -->

        <v-dialog
      v-model="editpassword"
      width="500"
    >
    <v-card>
        <v-card-text style="text-align: center;font-size:18px; padding-top:10px;">
        <v-text-field
            label="old password"
            solo
            required
            :maxlength="8"
            type="password"
            v-model="oldpassword"
          ></v-text-field>
          <v-text-field
            label="New password"
            solo
            required
            :maxlength="8"
            type="password"
            v-model="password"
          ></v-text-field>
          <v-text-field
            label="Confir new password"
            solo
            type="password"
            required
            :maxlength="8"
            v-model="passwordgood"
          ></v-text-field>
        </v-card-text>
        <v-action style="text-align:ltr;">
          <v-btn @click="editpassword = false; password = '' ; passwordgood = ''; oldpassword = '';" style="margin:0px 0px 5px 10px;background-color:grey;color:white;">cancel</v-btn>
          <v-btn @click="changePassword" style="float:right;margin-right:10px;background-color:red;color:white;">change</v-btn>
        </v-action>
      </v-card>
    </v-dialog>




    <!-- codes for do it -->
           <v-col cols="12" id="header">
               <div id="profile">
                <span><img id="image" src="../assets/unknown.png" style="width:50px; border-radius:50%; float:left;"></span>
                <span id="name"></span>
               </div>
               <div id="tools">
                    <ul id="listtools">
                        <li>New Job</li>
                        <li id="settings">Settings ^
                            <ul id="listsettings">
                                <li @click="dialog_err=true">Edit Profile</li>
                                <li @click="editname = true">Edit Name</li>
                                <li @click="editpassword = true;">Change Passsword</li>
                            </ul>
                        </li>
                        <li>About Me</li>


                        
                    </ul>
               </div>
           </v-col>







    </v-row>
</template>
<script>
export default {
name: 'AboutView',
    data: () => ({
      dialog_err:false,
      dialog: false,
      editname:false,
      firstName:"",
      lastName:"",
      userName:"",
      email:"",
      loginopen:true,
      logupopen:false,
      check:"",
      oldpassword:"",
      password:"",
      passwordgood:"",
      editpassword:false,

    }),
    mounted (){
        setInterval(() => {
            document.getElementById("tools").style.width = (window.innerWidth - 410) +"px"; 
        }, 1000); 
        document.getElementById("name").innerHTML = localStorage.getItem("firstname") + " " + localStorage.getItem("lastname");   
    },
    methods: {
        closeDialog(){
        this.dialog_err=false;
        document.getElementById("newimage").value = null;
      },
      changePorfile(){
      document.getElementById("image").src = document.getElementById("newimage").value;
      location.reload();
      },
      changeName(){
        if(this.firstName == "" || this.lastName == ""){
            alert("enter first name and last name");
        }else{
            localStorage.setItem("firstname",this.firstName);
            localStorage.setItem("lastname",this.lastName);
            location.reload();
        }
      },
      changePassword(){
        if(this.password == "" || this.passwordgood == "" || this.oldpassword == ""){
            alert("Fill all the fields");
        }else if(this.oldpassword != localStorage.getItem("password")){
            alert("your old paswords is not correct");
        }else if(this.password != this.passwordgood){
            alert("your paswords is not Identical");
        }else if(this.oldpassword == localStorage.getItem("password")){
            localStorage.setItem("password",this.password);
            alert("Done");
            setTimeout(() => {
                location.reload();
            }, 2000);    
        }    
      },
      
     },
}
</script>
<style scoped>
*{
    margin:0px;
    padding:0px;
}
#header{
    height:60px;
    background-color:rgb(0, 26, 255);
    box-shadow:1px 1px 10px 1px black;
}
#profile{
    padding:5px;
    display:inline-block;
    background-color:rgba(252, 63, 63, 0.801);
    border-right:solid 1px white;

}
#name{
    line-height:50px;
    margin-left:5px;
    display:inline-block;
    font-size:20px;
    color:white;
    padding-right:5px;
}
#tools{
    display:inline-block;
    height:60px;
    float:right;
}
#listtools{
    list-style:none;
    text-align: rtl;
}
#listtools li{
    display: inline-block;
    padding:5px;
    float:right;
    line-height:30px;
    font-size:18px;
    border-radius: 20px;
    margin:10px 10px 5px 0px ;
    color:white;
    transition:1s background;
    width:100px;
    text-align:center;
}
#listtools li:hover{
    cursor: pointer;
    box-shadow:1px 1px 10px 1px black;
}
#listsettings{
    list-style: none;
    position:absolute;
    width:200px;
    text-align:center;
    background-color: blue;
    box-shadow:1px 1px 10px 1px black;
    border-radius:10px;
    display:none;
}
#listsettings li{
    display: inline-block;
    padding:5px;
    line-height:30px;
    font-size:18px;
    border-radius: 20px;
    color:white;
    transition:1s background;
    width:170px;
    text-align:center;

}
#settings:hover #listsettings{
    display:block;
}
</style>