import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/HomeView.vue'
import location from "../views/location.vue"
Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: HomeView
  },
  {
    path: '/location',
    name: 'location',
    component: location
  },
]

const router = new VueRouter({
  routes
})

export default router
